.icon-marketing-section img {
  width: 60%;
  margin-top: 30px;
}

.services-section-title {
  font-size: 32px;
  padding-top: 60px;
  padding-bottom: 20px;
}

.icon-marketing-section {
  margin-top: 100px;
}

.icon-inner {
  height: 900px;
  background-color: white;
  background: url('./dust_scratches.png');
  width: 100%;
  padding-top: 110px;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 20px;
}

.icon-section-inner {
  padding-left: 60px;
  width: 90%;
  margin: auto;
}

.image-outer {
  width: 28%;
  height: auto;
  background-color: #ffffff;
  border: 1px solid #ccc;
  border-radius: 4px;
  float: left;
  padding-bottom: 30px;
  margin-right: 5%;
}

.icon-marketing-action-btn {
  clear: both;
  width: 240px;
  margin: auto;
  margin-top: -310px;
  margin-bottom: 190px;
}

.icon-marketing-link {
  color: white;
  text-decoration: none;
  background: #f47a27;
  color: white;
  font-size: 28px;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.marketing-title {
  margin-top: 10px;
}

.marketing-text {
  padding: 12px;
  font-size: 16px;
}

.flip-screen-text {
  font-size: 12px;
  padding-right: 10px;
  padding-left: 10px;
}

@media screen and (max-width:1100px) and (min-width:760px) {
  .icon-inner {
    margin-top: 150px;
    height: 900px;
    background-color: white;
    width: 100%;
    margin: auto;
  }
  .icon-section-inner {
    padding-left: 60px;
    width: 90%;
    margin: auto;
  }
  .image-outer {
    width: 27%;
    height: auto;
    border: 1px solid #ccc;
    border-radius: 4px;
    float: left;
    margin-right: 5%;
  }
  .icon-marketing-action-btn {
    clear: both;
    margin: auto;
    margin-top: -300px;
    margin-bottom: 160px;
  }
}

@media screen and (max-width:760px) and (min-width:400px) {
  .marketing-text {
    font-size: 15px;
  }
  .icon-inner {
    margin-top: 150px;
    height: 1850px;
    background-color: white;
    width: 100%;
    padding-top: 60px;
    margin: auto;
  }
  .icon-marketing-section img {
    width: 40%;
    margin-top: 30px;
  }
  .icon-section-inner {
    padding-left: 0px;
    width: 100%;
    margin: auto;
  }
  .image-outer {
    height: auto;
    float: none;
    width: 80%;
    margin: auto;
    margin-bottom: 25px;
  }
  .icon-marketing-action-btn {
    clear: both;
    margin: auto;
    margin-top: -420px;
    margin-bottom: 160px;
  }
}

@media screen and (max-width:400px) {
  .icon-inner {
    margin-top: 150px;
    height: 1850px;
    background-color: white;
    width: 100%;
    padding-top: 60px;
    margin: auto;
  }
  .icon-section-inner {
    padding-left: 0px;
    width: 100%;
    margin: auto;
  }
  .image-outer {
    height: auto;
    float: none;
    width: 80%;
    margin: auto;
    margin-bottom: 15px;
  }
  .icon-marketing-action-btn {
    clear: both;
    margin: auto;
    margin-top: -520px;
    margin-bottom: 140px;
  }
}


